function readExcels(fileList) {
  return new Promise((resolve, reject) => {
    let fileReader = new FileReader();
    let data = {};

    let readFile = (index) => {
      if (index < fileList.length) {
        fileReader.onload = (event) => {
          let workbook = XLSX.read(event.target.result, { type: 'binary' });
          let sheet = workbook.Sheets[workbook.SheetNames[0]];
          let cityName = fileList[index].name.substring(0, fileList[index].name.indexOf('.'));
          data[cityName] = XLSX.utils.sheet_to_json(sheet, { blankrows: false });
          readFile(index + 1);
        }
        fileReader.readAsBinaryString(fileList[index]);
      } else {
        resolve(data);
      }
    };
    readFile(0);
  });
}

let globalRowKey = 0;

function formatCityData(data, city) {
  let tableData = [];
  for (let statisticItem of DEFAULT_STATISTIC_ITEMS) {
    let rowData = {};
    globalRowKey++;
    rowData.key = globalRowKey;
    rowData[KEY_CITY_NAME] = city;
    rowData[KEY_STATISTIC_ITEM] = statisticItem;
    for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
      rowData[secondCategory] = data[secondCategory][statisticItem];
    }
    tableData.push(rowData);
  }
  return tableData;
}

