import {
  DEFAULT_STATISTIC_ITEMS,
  KEY_TICKET_TOTAL,
  NEW_ADD_CLOSE_RATE,
  REMAIN_CLOSE_RATE,
  OVERALL_CLOSE_RATE,
  RESPONSE_IN_30_MIN_RATE,
  RESPONSE_IN_30_HOUR_RATE,
  CLOSE_ON_TIME_RATE,
  TOTAL_OVERALL_CLOSE_RATE,
  STAGES,
  KEY_PROJECT_NAME,
  KEY_STATISTIC_ITEM,
  DEFAULT_SECOND_CATEGORIES
} from './constants';

export function filterProjectName(projectName) {
  let reg = /\(.*?\)|（.*?）/;
  let sub = projectName.match(reg);
  if (sub) {
    let newName = projectName.substring(0, projectName.indexOf(sub));
    return newName;
  } else {
    return projectName;
  }
}

export function parseTime(timeString) {
  let time = timeString.match(/\d+/g);
  let day = 0, hour = 0, min = 0;
  if (time && time.length > 0) {
    day = window.parseInt(time[0]);
    hour = window.parseInt(time[1]);
    min = window.parseInt(time[2]);
  }
  return { day, hour, min };
}

export function filterProject(projectName, reg) {
  let sub = projectName.match(reg);
  if (sub) {
    let newName = projectName.substring(0, projectName.indexOf(sub));
    return newName;
  } else {
    return projectName;
  }
}

export function filterProjectStage(projectName) {
  let filteredName = null;
  for (let stage of STAGES) {
    if (projectName.indexOf(stage) != -1) {
      filteredName = projectName.substring(0, projectName.indexOf(stage));
      return filteredName;
    }
  }
  return projectName;
}

export function formatToTable(data) {
  let tableData = [];
  var rowKey = 0;
  for (let projectName in data) {
    for (let statisticItem of DEFAULT_STATISTIC_ITEMS) {
      let rowData = {};
      rowKey++;
      rowData.key = rowKey;
      rowData[KEY_PROJECT_NAME] = projectName;
      rowData[KEY_STATISTIC_ITEM] = statisticItem;
      for (let secondCategory of [...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL]) {
        rowData[secondCategory] = data[projectName][secondCategory][statisticItem]
      }
      tableData.push(rowData);
    }
  }
  return tableData;
}

