import React from 'react';
import { Row } from 'antd';
// import the core library.
import ReactEchartsCore from 'echarts-for-react/lib/core';

// then import echarts modules those you have used manually.
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/pie';
// import 'echarts/lib/chart/scatter';
// import 'echarts/lib/chart/radar';

// import 'echarts/lib/chart/map';
// import 'echarts/lib/chart/treemap';
// import 'echarts/lib/chart/graph';
// import 'echarts/lib/chart/gauge';
// import 'echarts/lib/chart/funnel';
// import 'echarts/lib/chart/parallel';
// import 'echarts/lib/chart/sankey';
// import 'echarts/lib/chart/boxplot';
// import 'echarts/lib/chart/candlestick';
// import 'echarts/lib/chart/effectScatter';
// import 'echarts/lib/chart/lines';
// import 'echarts/lib/chart/heatmap';

// import 'echarts/lib/component/graphic';
// import 'echarts/lib/component/grid';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/tooltip';
// import 'echarts/lib/component/polar';
// import 'echarts/lib/component/geo';
// import 'echarts/lib/component/parallel';
// import 'echarts/lib/component/singleAxis';
// import 'echarts/lib/component/brush';

import 'echarts/lib/component/title';

// import 'echarts/lib/component/dataZoom';
// import 'echarts/lib/component/visualMap';

// import 'echarts/lib/component/markPoint';
// import 'echarts/lib/component/markLine';
// import 'echarts/lib/component/markArea';

// import 'echarts/lib/component/timeline';
import 'echarts/lib/component/toolbox';
// import { Rect } from 'echarts/lib/util/graphic';

// import 'zrender/lib/vml/vml';

import  { DEFAULT_SECOND_CATEGORIES } from '../constants';

function ChartView(props) {
  const titleConfig = {
    left: 'center',
    top: 0,
    textStyle: {
      color: '#000000'
    }
  };
  const toolboxConfig = {
    feature: {
      saveAsImage: { show: true }
    },
    right: 40
  };
  const legendConfig = {
    left: 40
  };
  const styles = {
    height: '500px',
  };

  let citySecondCategoryCloseAverageHandleTimeSeries = [];
  let newAddedCities = [];
  for (let cityName in props.citySecondCategoryCloseAverageHandleTimeData) {
    newAddedCities = [
      ...newAddedCities, cityName
    ];
    citySecondCategoryCloseAverageHandleTimeSeries = [
      ...citySecondCategoryCloseAverageHandleTimeSeries,
      {
        name: cityName,
        type: 'bar',
        barMaxWidth: 20,
        label: {
          position: 'top',
          color: '#333',
          show: true,
          formatter: '{c}'
        },
        data: props.citySecondCategoryCloseAverageHandleTimeData[cityName]
      }
    ];
  }

  let citySecondCategoryOpenAverageHandleTimeSeries = [];
  let openAverageHandleTimeCities = [];
  for (let cityName in props.citySecondCategoryOpenAverageHandleTimeData) {
    openAverageHandleTimeCities = [
      ...openAverageHandleTimeCities, cityName
    ];
    citySecondCategoryOpenAverageHandleTimeSeries = [
      ...citySecondCategoryOpenAverageHandleTimeSeries,
      {
        name: cityName,
        type: 'bar',
        barMaxWidth: 20,
        label: {
          position: 'top',
          color: '#333',
          show: true,
          formatter: '{c}'
        },
        data: props.citySecondCategoryOpenAverageHandleTimeData[cityName]
      }
    ];
  }

  return (
    <div>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各城市日常维修总体关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '累计受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '累计受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityTotalAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityOverallCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各项目日常维修总体关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.projects,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '累计受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '累计受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.projectTotalAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.projectOverallCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各项目分期日常维修总体关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.stages,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '累计受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '累计受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.stageTotalAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.stageOverallCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '剩余未关城市分布图',
              ...titleConfig
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            toolbox: toolboxConfig,
            series: [
              {
                name: '城市剩余未关',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)',
                  color: '#000'
                },
                data: props.cityOpenData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '剩余未关二级分类分布图',
              ...titleConfig
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            toolbox: toolboxConfig,
            series: [
              {
                name: '二级分类',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)',
                  color: '#000'
                },
                data: props.secondCategoryOpenData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各城市新增维修关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '新增受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '新增受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityNewAddedAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.cityNewAddedCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '项目新增关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.newAddedProjects,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '新增受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '新增受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.projectNewAddedAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.projectNewAddedCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '分期新增关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.newAddedStages,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '新增受理'
              },
              {
                type: 'value',
                name: '关闭率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '新增受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.stageNewAddedAcceptData
              },
              {
                name: '关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.stageNewAddedCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '新增维修城市分布图',
              ...titleConfig
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            toolbox: toolboxConfig,
            series: [
              {
                name: '城市',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)',
                  color: '#000'
                },
                data: props.cityNewAddedAcceptData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '新增维修二级分类分布图',
              left: 'center',
              textStyle: {
                color: '#000000'
              }
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b}:{c}({d}%)'
            },
            toolbox: toolboxConfig,
            series: [
              {
                name: '城市',
                type: 'pie',
                radius: '60%',
                label: {
                  formatter: '{b}:{c}({d}%)',
                  color: '#000'
                },
                data: props.secondCategoryNewAddedAcceptData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各城市需处理关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            legend: {
              data: ['需处理受理', '及时关闭率', '总体关闭率'],
              ...legendConfig
            },
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '需处理受理'
              },
              {
                type: 'value',
                name: '比率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '需处理受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityNeedHandleAcceptData
              },
              {
                name: '及时关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.cityNeedHandleCloseOnTimeRateData
              },
              {
                name: '总体关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.cityNeedHandleCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '各项目需处理关闭率',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            legend: {
              data: ['需处理受理', '及时关闭率', '总体关闭率'],
              ...legendConfig
            },
            xAxis: [
              {
                type: 'category',
                data: props.needHandleProjects,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '需处理受理'
              },
              {
                type: 'value',
                name: '比率',
                min: 0,
                max: 1
              }
            ],
            series: [
              {
                name: '需处理受理',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#000',
                  show: true,
                  formatter: '{c}'
                },
                data: props.projectNeedHandleAcceptData
              },
              {
                name: '及时关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.projectNeedHandleCloseOnTimeRateData
              },
              {
                name: '总体关闭率',
                type: 'line',
                yAxisIndex: 1,
                label: {
                  show: true,
                  color: '#000',
                  formatter: '{c}'
                },
                data: props.projectNeedHandleCloseRateData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '平均处理时长城市分布图',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: props.cities,
                axisPointer: {
                  type: 'shadow'
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '处理时长（天）'
              },
            ],
            legend: {
              data: ['已关闭', '剩余未关'],
              ...legendConfig
            },
            series: [
              {
                name: '已关闭',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityCloseAverageHandleTimeData
              },
              {
                name: '剩余未关',
                type: 'bar',
                barMaxWidth: 20,
                label: {
                  position: 'top',
                  color: '#333',
                  show: true,
                  formatter: '{c}'
                },
                data: props.cityOpenAverageHandleTimeData
              }
            ]
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '已关闭维修分类平均处理时长',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: DEFAULT_SECOND_CATEGORIES,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '处理时长（天）'
              },
            ],
            legend: {
              data: newAddedCities,
              ...legendConfig
            },
            series: citySecondCategoryCloseAverageHandleTimeSeries
          }}
        />
      </Row>
      <Row style={{margin: '40px 0'}}>
        <ReactEchartsCore
          style={styles}
          echarts={echarts}
          option={{
            title: {
              text: '剩余未关维修分类平均处理时长',
              ...titleConfig
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'cross',
                crossStyle: {
                  color: '#999'
                }
              }
            },
            toolbox: toolboxConfig,
            xAxis: [
              {
                type: 'category',
                data: DEFAULT_SECOND_CATEGORIES,
                axisPointer: {
                  type: 'shadow'
                },
                interval: 0,
                axisLabel: {
                  rotate: 45
                }
              },
            ],
            yAxis: [
              {
                type: 'value',
                name: '处理时长（天）'
              },
            ],
            legend: {
              data: openAverageHandleTimeCities,
              ...legendConfig
            },
            series: citySecondCategoryOpenAverageHandleTimeSeries
          }}
        />
      </Row>
    </div>
  );
}

export default ChartView;
