import React from 'react';
import { Table } from 'antd';
import {
  KEY_CITY_NAME,
  KEY_STATISTIC_ITEM,
  DEFAULT_SECOND_CATEGORIES,
  KEY_TICKET_TOTAL
} from '../constants';

function CityTable(props) {
  const cityColumn = (
    <Table.Column
      dataIndex={KEY_CITY_NAME}
      title={KEY_CITY_NAME}
      key={KEY_CITY_NAME}
      width={300}
    />
  );

  const otherColumns = [KEY_STATISTIC_ITEM,
    ...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL].map((item) => (
    <Table.Column
      dataIndex={item}
      title={item}
      key={item}
      width={300}
    />
  ));

  return (
    <Table
      title={(currentPageData) => { return props.title }}
      dataSource={props.filteredCities.length > 0 ?
        props.data.filter(item => props.filteredCities.indexOf(item[KEY_CITY_NAME]) != -1) : props.data}
      pagination={{ pageSize: 50 }}
      scroll={{ y: 400 }}
      size='small'
    >
      {cityColumn}
      {otherColumns}
    </Table>
  );
}

CityTable.defaultProps = {
  filteredCities: []
}

export default CityTable;
