import React from 'react';
import { Table } from 'antd';
import {
  KEY_STATISTIC_ITEM,
  DEFAULT_SECOND_CATEGORIES,
  KEY_TICKET_TOTAL
} from '../constants';

function AreaTable(props) {
  const otherColumns = [KEY_STATISTIC_ITEM,
    ...DEFAULT_SECOND_CATEGORIES, KEY_TICKET_TOTAL].map((item) => (
    <Table.Column
      dataIndex={item}
      title={item}
      key={item}
      width={300}
    />
  ));

  return (
    <Table
      title={(currentPageData) => { return props.title }}
      dataSource={props.data}
      pagination={{ pageSize: 50 }}
      scroll={{ y: 400 }}
      size='small'
    >
      {otherColumns}
    </Table>
  );
}

export default AreaTable;
